import React, { useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import TextField from '@material-ui/core/TextField';
import Typography from '@material-ui/core/Typography';
import { useEffect } from 'react';
import { useContext } from 'react';
import { GlobalContext } from '../../context/Context';
import DeleteIcon from '@material-ui/icons/Delete';
import Button from '@material-ui/core/Button';
import AddCompany from './AddCompany';
import { useNavigate } from "react-router-dom";

import axios from "axios";

const useStyles = makeStyles((theme) => ({
    root: {
        flexGrow: 1,
    },
    paper: {
        padding: theme.spacing(2),
        textAlign: 'center',
        color: theme.palette.text.secondary,
    },
    textField: {
        marginLeft: theme.spacing(1),
        marginRight: theme.spacing(1),
    },
    button: {
        margin: theme.spacing(1),
    },

}));
export default function Companies() {
    const classes = useStyles();
    const navigate = useNavigate();

    // Dispatch function from Context api to toggle state
    let { dispatch } = useContext(GlobalContext);

    // Deleting button function
    function handleDelete(id) {
        axios.delete(`https://hierarchy-asset.herokuapp.com/company/${id}`)
            .then((axResponse) => {
                alert(axResponse.data)
                dispatch({
                    type: "COMPANY_ADD",
                })
            })
            .catch((err) => {
                alert(err.request.response);
                return;
            })
    }
    let [data, setData] = useState();

    // Global State
    let { state } = useContext(GlobalContext);

    useEffect(() => {
        async function apiCall() {
            const response = await fetch(state.baseUrl + "/companies");
            const resJson = await response.json();
            return setData(resJson)
        }
        apiCall();
    }, [state.refreshCompany]);
    return (
        <>
            <div style={{ width: "100%", marginBottom: '1rem', textAlign: "left" }}>
                <Typography variant="h4" style={{ color: "black", marginTop: "1rem" }}>
                    Companies :
                </Typography>
            </div>
            {data ?
                data.map((eachCompany, ind) =>
                    <Paper key={ind} className={classes.paper} style={{ border: "3px solid grey", maxWidth: "40rem", marginBottom: '1rem', textAlign: "left" }}>
                        <div style={{ display: "flex", justifyContent: "space-between", marginBottom: "1rem" }}>
                            <Typography
                                onClick={() => {
                                    navigate(`/${eachCompany._id}`);
                                }}
                                variant="h4" style={{ color: "black", cursor: "pointer" }}>
                                {eachCompany.name}
                            </Typography>
                            <Button
                                variant="outlined"
                                color="secondary"
                                className={classes.button}
                                startIcon={<DeleteIcon />}
                                onClick={() => handleDelete(eachCompany._id, eachCompany)}
                            >
                                Delete
                            </Button>
                        </div>
                        <TextField
                            className={classes.textField}
                            label="Company Address"
                            InputProps={{
                                readOnly: true,
                                disableUnderline: true,
                                disabled: true
                            }}
                            defaultValue={eachCompany.address}
                            style={{ marginBottom: " 1rem " }}
                        />
                        <TextField
                            className={classes.textField}
                            label="Company Email"
                            defaultValue={eachCompany.email}
                            InputProps={{
                                readOnly: true,
                                disableUnderline: true,
                                disabled: true
                            }}
                            style={{ marginBottom: " 1rem " }}
                        />
                        <TextField
                            className={classes.textField}
                            InputProps={{
                                readOnly: true,
                                disableUnderline: true,
                                disabled: true
                            }}
                            label="Company Website"
                            defaultValue={eachCompany.website}
                        />
                        <TextField
                            className={classes.textField}
                            InputProps={{
                                readOnly: true,
                                disableUnderline: true,
                                disabled: true
                            }}
                            defaultValue={eachCompany.phone}
                            label="Company Phone"
                        />
                    </Paper>
                )
                : <h2>Loading....</h2>}
            <AddCompany></AddCompany>
        </>
    )
}
