import React, { useContext } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import TextField from '@material-ui/core/TextField';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import { useState } from 'react';
import Accordion from '@material-ui/core/Accordion';
import AccordionSummary from '@material-ui/core/AccordionSummary';
import AccordionDetails from '@material-ui/core/AccordionDetails';
import AddCircleOutlineIcon from '@material-ui/icons/AddCircleOutline';
import { GlobalContext } from '../../context/Context';
import axios from "axios"

const useStyles = makeStyles((theme) => ({
    root: {
        flexGrow: 1,
        width: "100 %",
    },
    heading: {
        fontSize: theme.typography.pxToRem(15),
        fontWeight: theme.typography.fontWeightRegular,
    },
    paper: {
        padding: theme.spacing(2),
        textAlign: 'center',
        color: theme.palette.text.secondary,
    },
    textField: {
        marginLeft: theme.spacing(1),
        marginRight: theme.spacing(1),
    },
}));

export default function AddAsset({ groupId }) {
    const classes = useStyles();
    // Local States 
    let [name, setName] = useState();
    let [plateNumber, setPlateNumber] = useState();
    let [serialNo, setSerialNo] = useState();
    let [make, setMake] = useState();
    let [model, setModel] = useState();
    let [year, setYear] = useState();
    let [oilType, setOilType] = useState();
    let [type, setType] = useState();
    let [horsePower, setHorsePower] = useState();
    let purchasingDate = new Date().getTime()
    // console.log("Purchasing date : ",purchasingDate)
    // 1604507595383 (in javascript e.g: new Date().getTime() ),

    let { dispatch, state } = useContext(GlobalContext);
    const handleSubmit = (e) => {
        e.preventDefault();

        axios.post(state.baseUrl + "/asset", {
            name, groupId, plateNumber, make, model, year, oilType, type, horsePower, purchasingDate
        }).then((res) => {
            dispatch({
                type: "PARENTASSESTS_ADD",
            })
            alert(res.request.response)
        }
        )
        setName("");
        setPlateNumber("");
        setSerialNo("")
        setMake("");
        setModel("")
        setYear("")
        setOilType("");
        setType("");
        setHorsePower("");
    }
    return (
        <>
            <Accordion style={{ border: "1px solid grey", width: "42rem", marginBottom: '1rem', textAlign: "left", backgroundColor: "lavender", marginTop: "1rem", }}>
                <AccordionSummary
                    aria-controls="panel1a-content"
                    id="panel1a-header"
                    style={{ display: 'flex', justifyContent: "center", alignItems: "center" }}
                >
                    <Typography variant="h6" style={{ color: "Black" }}>
                        Add New Asset
                    </Typography>
                    <AddCircleOutlineIcon style={{ color: "green", paddingTop: "0.3rem", paddingLeft: "0.5rem" }}></AddCircleOutlineIcon>
                </AccordionSummary>
                <AccordionDetails >
                    <div style={{ textAlign: "left" }}>
                        <Typography variant="h4" gutterBottom style={{ color: "black" }}>
                            Register Asset
                            </Typography>
                        <form autoComplete="off" onSubmit={handleSubmit}>
                            <TextField className={classes.textField} value={name} onInput={(e) => setName(e.target.value)} required label="Name" />

                            <TextField className={classes.textField} value={plateNumber} onInput={(e) => setPlateNumber(e.target.value)} label="Plate Number" />

                            <TextField className={classes.textField} value={serialNo} onInput={(e) => setSerialNo(e.target.value)} label="Serial No" />

                            <TextField className={classes.textField} value={make} onInput={(e) => setMake(e.target.value)} label="Make" />
                            <TextField className={classes.textField} value={model} onInput={(e) => setModel(e.target.value)} label="Model" />
                            <TextField className={classes.textField} value={year} onInput={(e) => setYear(e.target.value)} label="Year" />

                            <TextField className={classes.textField} value={oilType} onInput={(e) => setOilType(e.target.value)} label="Oil Type" />

                            <TextField className={classes.textField} value={type} onInput={(e) => setType(e.target.value)} label="Type" />

                            <TextField className={classes.textField} value={horsePower} onInput={(e) => setHorsePower(e.target.value)} label="Horse Power" />

                            <span style={{ width: "100%", display: "inline-block", marginTop: "1.5rem" }}>
                                <Button type="submit" variant="contained" color="primary"
                                    style={{ width: "50%" }}>
                                    Create Asset
                                    </Button>
                            </span>
                        </form>
                    </div>
                </AccordionDetails>
            </Accordion>
        </>
    )
}
