import React, { useState } from 'react';
import Typography from '@material-ui/core/Typography';
import { useEffect } from 'react';
import { useContext } from 'react';
import { GlobalContext } from '../../context/Context';
import { useParams } from 'react-router';
import Chips from "../chip/Chips"
import axios from "axios"
import { makeStyles } from '@material-ui/core/styles';
import TextField from '@material-ui/core/TextField';


const useStyles = makeStyles((theme) => ({
    textField: {
        marginLeft: theme.spacing(1),
        marginRight: theme.spacing(1),
    },

}));


export default function ChildAsset() {
    const classes = useStyles();
    let [data, setData] = useState();
    // Global State
    let { state } = useContext(GlobalContext);
    const { childAssetId } = useParams();

    // Hitting API code
    useEffect(() => {
        (async () => {
            const response = await axios.get(state.baseUrl + "/child-asset/" + childAssetId);
            setData(response.data)
        })()
    }, [state.refreshChips, childAssetId]);
    // console.log("server dAta", data)
    return (
        <>
            {data ?
                <div style={{ width: "100%", marginBottom: '1rem', textAlign: "left" }}>
                    <Typography variant="h4" style={{ color: "black", marginTop: "1rem", marginBottom: "1rem" }}>
                        {data.name} :
                    </Typography>
                   
                    <TextField className={classes.textField}
                        InputProps={{ disableUnderline: true }}
                        value={data.type} label="Type" disabled/>
                    <TextField className={classes.textField}
                        InputProps={{ disableUnderline: true }}
                        value={data.serialNo} label="Serial Number" disabled/>
                        
                    <TextField className={classes.textField}
                        InputProps={{ disableUnderline: true }}
                        value={data.make} label="Make" disabled/>
                        
                    <TextField className={classes.textField}
                        InputProps={{ disableUnderline: true }}
                        value={data.model} label="Model" disabled/>

                    <TextField className={classes.textField}
                        InputProps={{ disableUnderline: true }}
                        value={data.year} label="Year" disabled/>

                </div>
                : <h2>Loading Child Asset...</h2>
            }
            <Chips></Chips>
        </>
    )
}