import React, { useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';
import { useEffect } from 'react';
import { useContext } from 'react';
import { GlobalContext } from '../../context/Context';
import DeleteIcon from '@material-ui/icons/Delete';
import Button from '@material-ui/core/Button';
import { useNavigate, useParams } from 'react-router';
import axios from "axios"
import AddChildAsset from './AddChildAsset'
import TextField from '@material-ui/core/TextField';


const useStyles = makeStyles((theme) => ({
    root: {
        flexGrow: 1,
    },
    paper: {
        padding: theme.spacing(2),
        textAlign: 'center',
        color: theme.palette.text.secondary,
    },
    textField: {
        marginLeft: theme.spacing(1),
        marginRight: theme.spacing(1),
    },
    button: {
        margin: theme.spacing(1),
    },
}));
export default function ChildAssets() {
    const classes = useStyles();
    const navigate = useNavigate()

    // States
    const { assetId } = useParams()
    let [data, setData] = useState([]);
    // console.log("Data", data)
    // Global State
    let { state, dispatch } = useContext(GlobalContext);

    // Deleting asset function
    function handleDelete(id) {
        axios.delete(state.baseUrl + `/child-asset/${id}`)
            .then((res) => {
                alert(res.request.response)
                dispatch({
                    type: "CHILDASSESTS_ADD",
                })
            }
            ).catch((err) => alert(err.request.response))
    }

    // Hitting API code
    useEffect(() => {
        async function apiCall() {
            const response = await axios.get(state.baseUrl + "/child-assets?assetId=" + assetId);
            setData(response)
        }
        apiCall()
    }, [state.refreshChildAssests, state.refreshChips, assetId]);

    return (
        <>
            {data.data ? data.data.map((eachChildAsset, ind) =>
                <Paper key={ind} className={classes.paper} style={{ border: "3px solid grey", width: "40rem", marginBottom: '1rem', textAlign: "left" }}>
                    <div style={{ display: "flex", justifyContent: "space-between", marginBottom: "1rem" }}>
                        <Typography variant="h4" style={{ color: "black", cursor: "pointer" }}
                            onClick={() => {
                                navigate(`/childAsset/${eachChildAsset._id}`);
                            }}
                        >
                            {/* Group name here maping */}
                            {eachChildAsset.name}
                        </Typography>
                        <Button
                            variant="outlined"
                            color="secondary"
                            className={classes.button}
                            startIcon={<DeleteIcon />}
                            onClick={() => handleDelete(eachChildAsset._id)}
                        >
                            Delete
                            </Button>
                    </div>
                    <TextField className={classes.textField}
                        InputProps={{ disableUnderline: true }}
                        value={eachChildAsset.type} label="Type" disabled/>
                    <TextField className={classes.textField}
                        InputProps={{ disableUnderline: true }}
                        value={eachChildAsset.serialNo} label="Serial Number" disabled/>
                        
                    <TextField className={classes.textField}
                        InputProps={{ disableUnderline: true }}
                        value={eachChildAsset.make} label="Make" disabled/>
                        
                    <TextField className={classes.textField}
                        InputProps={{ disableUnderline: true }}
                        value={eachChildAsset.model} label="Model" disabled/>

                    <TextField className={classes.textField}
                        InputProps={{ disableUnderline: true }}
                        value={eachChildAsset.year} label="Year" disabled/>

                </Paper>)
                : <h2>Loading Assets.......</h2>}

            <AddChildAsset assetId={assetId}></AddChildAsset>
        </>
    )
}
