export const reducer = (state, action) => {
    switch (action.type) {
        case "TOGGLE": {
            return { ...state, bool: !state.bool }
        }
        case "COMPANY_ADD": {
            return { ...state, refreshCompany: !state.refreshCompany }
        }
        case "GROUP_ADD": {
            return { ...state, refreshGroup: !state.refreshGroup }
        }
        case "PARENTASSESTS_ADD": {
            return { ...state, refreshParentAssests: !state.refreshParentAssests }
        }
        case "CHILDASSESTS_ADD": {
            return { ...state, refreshChildAssests: !state.refreshChildAssests }
        }
        case "CHIPS_ADD": {
            return { ...state, refreshChips: !state.refreshChips }
        }
        case "SENSORS_ADD": {
            return { ...state, refreshSensors: !state.refreshSensors }
        }
        default: {
            return state
        }
    }
}