import React, { useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';
import { useEffect } from 'react';
import { useContext } from 'react';
import { GlobalContext } from '../../context/Context';
import DeleteIcon from '@material-ui/icons/Delete';
import Button from '@material-ui/core/Button';
import { useParams } from 'react-router';
import AddGroup from '../group/AddGroup';
import axios from "axios"
import { useNavigate } from "react-router-dom";
import TextField from '@material-ui/core/TextField';


const useStyles = makeStyles((theme) => ({
    root: {
        flexGrow: 1,
    },
    paper: {
        padding: theme.spacing(2),
        textAlign: 'center',
        color: theme.palette.text.secondary,
    },
    textField: {
        marginLeft: theme.spacing(1),
        marginRight: theme.spacing(1),
    },
    button: {
        margin: theme.spacing(1),
    },
}));
export default function Groups() {
    const navigate = useNavigate();

    const classes = useStyles();

    // States
    const { companyId } = useParams()
    let [data, setData] = useState([]);
    // Global State
    let { state, dispatch } = useContext(GlobalContext);

    // Deleting groups function
    function handleDelete(id) {
        axios.delete(state.baseUrl + `/group/${id}`)
            .then((res) => {
                alert(res.request.response)
                dispatch({
                    type: "GROUP_ADD",
                })
            }
            ).catch((err) => alert(err.request.response))
    }

    // Hitting API code
    useEffect(() => {
        async function apiCall() {
            const response = await axios.get(state.baseUrl + "/groups?companyId=" + companyId);
            return setData(response)
        }
        apiCall()
    }, [state.refreshGroup, companyId]);
    // console.log("Data:",data)

    return (
        <>
            {data.data ? data.data.map((eachGroup, ind) =>
                <Paper key={ind} className={classes.paper} style={{ border: "3px solid grey", width: "40rem", marginBottom: '1rem', textAlign: "left" }}>
                    <div style={{ display: "flex", justifyContent: "space-between" }}>
                        <Typography onClick={() => {
                            navigate(`/group/${eachGroup._id}`);
                        }} variant="h4" style={{ color: "black", cursor: "pointer" }}>
                            {/* Group name here maping */}
                            {eachGroup.name}
                        </Typography>
                        <Button
                            variant="outlined"
                            color="secondary"
                            className={classes.button}
                            startIcon={<DeleteIcon />}
                            onClick={() => handleDelete(eachGroup._id)}
                        >
                            Delete
                            </Button>
                    </div>
                    <TextField
                        className={classes.textField}
                        label="Account Number"
                        InputProps={{
                            readOnly: true,
                            disableUnderline: true,
                            disabled: true
                        }}
                        defaultValue={eachGroup.accountNumber}
                        // style={{ marginBottom: " 1rem " }}
                    />
                </Paper>)
                : <h2>Loading Groups.....</h2>}

            <AddGroup companyId={companyId}></AddGroup>
        </>
    )
}
