import React, { useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import { useEffect } from 'react';
import { useContext } from 'react';
import { GlobalContext } from '../../context/Context';
import { useParams } from 'react-router';
import Groups from '../group/Groups';
import TextField from '@material-ui/core/TextField';

const useStyles = makeStyles((theme) => ({
    textField: {
        marginLeft: theme.spacing(1),
        marginRight: theme.spacing(1),
    },

}));

export default function Company() {
    const classes = useStyles();
    const { companyId } = useParams()
    let [data, setData] = useState();
    // Global State
    let { state } = useContext(GlobalContext);

    // Hitting API code
    useEffect(() => {
        (async function apiCall() {
            const response = await fetch(state.baseUrl + "/companies");
            const resJson = await response.json();
            setData(resJson)
        })()

    }, [state.refreshGroup]);

    // Filtering data for getting one company name 
    let filterData = data ? data.filter((eachItem) => eachItem._id == companyId) : null;

    // console.log("Server data:", filterData)
    return (
        <>
            { filterData ?
                filterData.map((eachCompany, ind) =>
                    <div key={ind} style={{ width: "100%", marginBottom: '1rem', textAlign: "left" }}>
                        <Typography variant="h4" style={{ color: "black", marginTop: "1rem" ,marginBottom:"1rem"}}>
                            {eachCompany.name} :
                        </Typography>
                        <TextField
                            className={classes.textField}
                            label="Company Address"
                            InputProps={{
                                readOnly: true,
                                disableUnderline: true,
                                disabled: true
                            }}
                            defaultValue={eachCompany.address}
                            style={{ marginBottom: " 1rem " }}
                        >
                        </TextField>
                        <TextField
                            className={classes.textField}
                            label="Company Email"
                            InputProps={{
                                readOnly: true,
                                disableUnderline: true,
                                disabled: true
                            }}
                            defaultValue={eachCompany.email}
                            style={{ marginBottom: " 1rem " }}
                        >
                        </TextField>
                        <br/>
                        <TextField
                            className={classes.textField}
                            label="Company Phone"
                            InputProps={{
                                readOnly: true,
                                disableUnderline: true,
                                disabled: true
                            }}
                            defaultValue={eachCompany.phone}
                            style={{ marginBottom: " 1rem " }}
                        >
                        </TextField>
                        <TextField
                            className={classes.textField}
                            label="Company Website"
                            InputProps={{
                                readOnly: true,
                                disableUnderline: true,
                                disabled: true
                            }}
                            defaultValue={eachCompany.website}
                            style={{ marginBottom: " 1rem " }}
                        >
                        </TextField>
                    </div>
                )
                : <h2>Loading...</h2>}
            <Groups></Groups>
        </>
    )
}
