import React, { useContext, useEffect, useState } from 'react';
import { makeStyles, withStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import { GlobalContext } from '../../context/Context';
import { useParams } from 'react-router';
import axios from "axios"
import InfiniteScroll from "react-infinite-scroll-component"
import io from "socket.io-client";
import { baseUrl } from '../../core'

const socket = io(baseUrl);

const StyledTableCell = withStyles((theme) => ({
    head: {
        backgroundColor: theme.palette.common.black,
        color: theme.palette.common.white,
    },
    body: {
        fontSize: 14,
    },
}))(TableCell);
const StyledTableRow = withStyles((theme) => ({
    root: {
        '&:nth-of-type(odd)': {
            backgroundColor: theme.palette.action.hover,
        },
    },
}))(TableRow);

const useStyles = makeStyles({
    table: {
        minWidth: 650,
    },
});

export default function SensorReading() {
    const classes = useStyles();

    let [rows, setRows] = useState([])

    // Global State
    let { state } = useContext(GlobalContext);

    let { sensorId } = useParams();
    // console.log("Sensor id", sensorId)

    let [hasMoreVar, sethasMoreVar] = useState(false);

    useEffect(() => {
        (async function apiCall() {
            let response = await axios.get(state.baseUrl + "/readings?sensorId=" + sensorId + "&page=" + "0").catch((err) => console.log(err));
            sethasMoreVar(response.data.length % 100 === 0)
            setRows(response.data)
        })()
        // Socket implementation
        socket.on(sensorId, data => {
            setRows(rows => [data.data, ...rows]);
        })
    }, [sensorId])

    async function secondCall() {
        let response = await axios.get(state.baseUrl + "/readings?sensorId=" + sensorId + "&page=" + rows.length).catch((err) => console.log(err));
        { sethasMoreVar(response.data.length % 100 == 0) }
        setRows(rows.concat(response.data))
    }

    function dateParser(dateString) {
        let temp = new Date(dateString)
        let day = temp.getDate() + 1;
        let month = temp.getMonth() + 1;
        let year = temp.getFullYear();
        let hours = temp.getHours();
        let minutes = temp.getMinutes();
        let seconds = temp.getSeconds();
        return day + "-" + month + "-" + year + " " + hours + ":" + minutes + ":" + seconds;
        // return "12-jan-2020 2:30:24"
    }

    if (rows.length !== 0) {
        return (
            <InfiniteScroll dataLength={rows.length} hasMore={hasMoreVar} next={secondCall}>
                <TableContainer component={Paper}>
                    <Table id="table" className={classes.table} aria-label="simple table">
                        <TableHead>
                            <TableRow>
                                <TableCell align="center" style={{ fontWeight: "bold", textTransform: "uppercase" }}>Created On</TableCell>
                                <TableCell align="center"
                                    style={{ fontWeight: "bold", textTransform: "uppercase" }}
                                >Reading</TableCell>
                            </TableRow>
                        </TableHead>
                        <TableBody>
                            {rows !== 0 ? rows.map((row, ind) => (
                                <StyledTableRow key={ind}>
                                    <StyledTableCell align="center">
                                        {dateParser(row.createdOn)}
                                    </StyledTableCell>
                                    <StyledTableCell align="center">{row.reading}</StyledTableCell>
                                </StyledTableRow>
                            )) : null}
                        </TableBody>
                    </Table>
                </TableContainer>
            </InfiniteScroll >
        );
    } else {
        return null
    }
}