import React, { useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';
import { useEffect } from 'react';
import { useContext } from 'react';
import { GlobalContext } from '../../context/Context';
import DeleteIcon from '@material-ui/icons/Delete';
import Button from '@material-ui/core/Button';
import { useNavigate, useParams } from 'react-router';
import axios from "axios"
import AddSensor from './AddSensor'
import TextField from '@material-ui/core/TextField';


const useStyles = makeStyles((theme) => ({
    root: {
        flexGrow: 1,
    },
    paper: {
        padding: theme.spacing(2),
        textAlign: 'center',
        color: theme.palette.text.secondary,
    },
    textField: {
        marginLeft: theme.spacing(1),
        marginRight: theme.spacing(1),
    },
    button: {
        margin: theme.spacing(1),
    },
}));
export default function Chips() {
    const classes = useStyles();
    const navigate = useNavigate()

    // States
    const { chipsetId } = useParams()
    // console.log("Use param:",assetId)
    let [data, setData] = useState([]);
    // Global State
    let { state, dispatch } = useContext(GlobalContext);
    // Dispatch function from Context api to toggle state

    // Deleting asset function

    function handleDelete(id) {
        axios.delete(state.baseUrl + `/sensor/${id}`)
            .then((res) => {
                alert(res.request.response)
                dispatch({
                    type: "SENSORS_ADD",
                })
            }
            ).catch((err) => alert(err.request.response))
    }
    // Hitting API code
    useEffect(() => {
        async function apiCall() {
            const response = await axios.get(state.baseUrl + "/sensors?chipsetId=" + chipsetId);
            setData(response)
        }
        apiCall()
    }, [state.refreshChips, state.refreshSensors, chipsetId]);

    return (
        <>
            {data.data ? data.data.map((eachSensor, ind) =>
                <Paper key={ind} className={classes.paper} style={{ border: "3px solid grey", width: "40rem", marginBottom: '1rem', textAlign: "left" }}>
                    <div style={{ display: "flex", justifyContent: "space-between" }}>
                        <Typography variant="h4" style={{ color: "black", cursor: "pointer" }}

                            onClick={() => {
                                navigate(`/sensor/${eachSensor._id}`);
                            }}>
                            {/* Group name here maping */}
                            {eachSensor.name}
                        </Typography>
                        <Button
                            variant="outlined"
                            color="secondary"
                            className={classes.button}
                            startIcon={<DeleteIcon />}
                            onClick={() => handleDelete(eachSensor._id)}
                        >
                            Delete
                            </Button>
                    </div>

                    <TextField className={classes.textField}
                        InputProps={{ disableUnderline: true }}
                        value={eachSensor.type} label="Type" disabled />
                        
                    <TextField className={classes.textField}
                        InputProps={{ disableUnderline: true }}
                        value={eachSensor.serialNo} label="Serial Number" disabled />

                    <TextField className={classes.textField}
                        InputProps={{ disableUnderline: true }}
                        value={eachSensor.make} label="Make" disabled />

                    <TextField className={classes.textField}
                        InputProps={{ disableUnderline: true }}
                        value={eachSensor.model} label="Model" disabled />

                    <TextField className={classes.textField}
                        InputProps={{ disableUnderline: true }}
                        value={eachSensor.year} label="Year" disabled />




                </Paper>)
                : <h2>Loading Sensors.......</h2>}
            <AddSensor chipsetId={chipsetId}></AddSensor>
        </>
    )
}
