import React, { useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';
import { useEffect } from 'react';
import { useContext } from 'react';
import { GlobalContext } from '../../context/Context';
import DeleteIcon from '@material-ui/icons/Delete';
import Button from '@material-ui/core/Button';
import { useParams } from 'react-router';
import AddAsset from './AddAsset'
import axios from "axios"
import { useNavigate } from "react-router-dom";
import TextField from '@material-ui/core/TextField';

const useStyles = makeStyles((theme) => ({
    root: {
        flexGrow: 1,
    },
    paper: {
        padding: theme.spacing(2),
        textAlign: 'center',
        color: theme.palette.text.secondary,
    },
    textField: {
        marginLeft: theme.spacing(1),
        marginRight: theme.spacing(1),
    },
    button: {
        margin: theme.spacing(1),
    },
}));
export default function Assets() {
    const classes = useStyles();
    const navigate = useNavigate();

    // States
    const { groupId } = useParams()
    let [data, setData] = useState([]);
    // Global State
    let { state, dispatch } = useContext(GlobalContext);

    // Deleting asset function
    function handleDelete(id) {
        axios.delete(state.baseUrl + `/asset/${id}`)
            .then((res) => {
                alert(res.request.response)
                dispatch({
                    type: "PARENTASSESTS_ADD",
                })
            }
            ).catch((err) => alert(err.request.response))
    }

    useEffect(() => {
        async function apiCall() {
            const response = await axios.get(state.baseUrl + "/assets?groupId=" + groupId);
            setData(response)
        }
        apiCall()

    }, [state.refreshParentAssests, groupId]);
    // console.log("server response", data)
    return (
        <>
            {data.data ? data.data.map((eachAsset, ind) =>
                <Paper key={ind} className={classes.paper} style={{ border: "3px solid grey", width: "40rem", marginBottom: '1rem', textAlign: "left" }}>
                    <div style={{ display: "flex", justifyContent: "space-between" }}>
                        <Typography variant="h4" style={{ color: "black", cursor: "pointer" }}
                            onClick={() => {
                                navigate(`/asset/${eachAsset._id}`);
                            }}
                        >
                            {/* Group name here maping */}
                            {eachAsset.name}
                        </Typography>

                        <Button
                            variant="outlined"
                            color="secondary"
                            className={classes.button}
                            startIcon={<DeleteIcon />}
                            onClick={() => handleDelete(eachAsset._id)}
                        >
                            Delete
                            </Button>
                    </div>
                    <TextField className={classes.textField} InputProps={{ disableUnderline: true }} label="Plate Number" value={eachAsset.plateNumber} disabled />
                    <TextField className={classes.textField} InputProps={{ disableUnderline: true }} label="Serial Number" value={eachAsset.serialNumber} disabled />
                    <TextField className={classes.textField} InputProps={{ disableUnderline: true }} label="Make" value={eachAsset.make} disabled />
                    <TextField className={classes.textField} InputProps={{ disableUnderline: true }} label="Model" value={eachAsset.model} disabled />
                    <TextField className={classes.textField} InputProps={{ disableUnderline: true }} label="Year" value={eachAsset.year} disabled />
                    <TextField className={classes.textField} InputProps={{ disableUnderline: true }} label="Oil Type" value={eachAsset.oilType} disabled />
                    <TextField className={classes.textField} InputProps={{ disableUnderline: true }} label="Type" value={eachAsset.type} disabled />
                    <TextField className={classes.textField} InputProps={{ disableUnderline: true }} label="Horse Power" value={eachAsset.horsePower} disabled />
                </Paper>)
                : <h2>Loading Assets.......</h2>}

            <AddAsset groupId={groupId}></AddAsset>
        </>
    )
}
