import React from 'react';
import Grid from '@material-ui/core/Grid';
import Companies from './company/Companies';
import Company from './company/Company';
import { Routes, Route } from "react-router-dom";
import Group from './group/Group';
import Asset from './asset/Asset';
import ChildAsset from "./childAsset/ChildAsset"
import Chip from './chip/Chip'
import Sensor from './sensors/Sensor'

export default function RightBar() {
    return (
        <Grid item xs={9} style={{ display: "flex", justifyContent: 'start', alignItems: "center", flexDirection: "column", overflow: 'hidden', textTransform: "capitalize", }}>
            <Routes>
                <Route path="/" element={<Companies />} />
                <Route path="/companies" element={<Companies />} />
                <Route path="/:companyId" element={<Company />} />
                <Route path="/group/:groupId" exact  element={<Group />} />
                <Route path="/asset/:assetId" exact element={<Asset />} />
                <Route path="/childAsset/:childAssetId" exact element={<ChildAsset />} />
                <Route path="/chip/:chipsetId" exact element={<Chip />} />
                <Route path="/sensor/:sensorId" exact element={<Sensor />} />
                <Route path="*" element={<h2>page not found</h2>} />
            </Routes>
        </Grid >
    );
}
