import React, { useEffect, useState } from "react";
import { makeStyles } from "@material-ui/core/styles";
import TreeView from "@material-ui/lab/TreeView";
import ExpandMoreIcon from "@material-ui/icons/ExpandMore";
import ChevronRightIcon from "@material-ui/icons/ChevronRight";
import TreeItem from "@material-ui/lab/TreeItem";
import Grid from '@material-ui/core/Grid';
import { useContext } from 'react';
import { GlobalContext } from '../context/Context';
import { useNavigate } from "react-router-dom";
import axios from "axios"

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
    maxWidth: 400,
    color: "Black",
  },
  paper: {
    padding: theme.spacing(2),
    textAlign: 'left',
    color: theme.palette.text.secondary,
  },
}
));

export default function SideBar2() {
  const navigate = useNavigate();
  const classes = useStyles();
  const [data, setData] = useState();
  let { state } = useContext(GlobalContext);
  const [expanded, setExpanded] = useState([]);
  const [selected, setSelected] = useState([]);

  const handleToggle = (event, nodeIds) => {
    const A = nodeIds;
    const B = expanded;
    const clicked = A.filter((n) => !B.includes(n))[0];

    const _A = expanded;
    const _B = nodeIds;
    const closed = _A.filter((n) => !_B.includes(n))[0];

    if (clicked) {
      setExpanded(nodeIds);
    } else if (selected !== closed) {
      setSelected(nodeIds);
    } else {
      setExpanded(nodeIds);
    }
  };

  const handleSelect = (event, nodeIds) => {
    setSelected(nodeIds);
  };

  useEffect(() => {
    async function apiCall() {
      const response = await axios.get(state.baseUrl + "/nav")
      return setData(response)
    }
    apiCall()
  }, [state.refreshCompany, state.refreshGroup, state.refreshParentAssests, state.refreshChildAssests, state.refreshChips, state.refreshSensors])

  return (
    <Grid item xs={3} style={{}}>
      <TreeView
        expanded={expanded}
        selected={selected}
        onNodeToggle={handleToggle}
        onNodeSelect={handleSelect}
        className={classes.root}
        defaultCollapseIcon={<ExpandMoreIcon />}
        defaultExpandIcon={<ChevronRightIcon />}
        style={{ position: "fixed", height: "100vh", overflowY: "scroll", width: "20rem", marginTop: "0.5rem", marginLeft: "0.5rem" }}
      >
        <TreeItem
          nodeId="101" label="Companies" onClick={() => {
            navigate(`/companies`);
          }}>
          {data ? data.data.map((eachCompany, companyInd) =>
            // company name
            <TreeItem
              nodeId={eachCompany._id} key={companyInd} label={eachCompany.name}
              onClick={() => {
                navigate(`/${eachCompany._id}`);
              }}
            >
              {/* Group name */}
              {eachCompany.groups.map((eachGroup, groupInd) =>
                <TreeItem
                  nodeId={eachGroup._id} key={groupInd} label={eachGroup.name} onClick={() => {
                    navigate(`/group/${eachGroup._id}`);
                  }}>
                  {/* Assets Name */}
                  {eachGroup.assets.map((eachAsset, assetInd) =>
                    <TreeItem
                      nodeId={eachAsset._id} key={assetInd} label={eachAsset.name} onClick={() => {
                        navigate(`/asset/${eachAsset._id}`);
                      }}>
                      {/* childAsset Name */}
                      {eachAsset.childAssets.map((eachChildAsset, childAssetInd) =>
                        <TreeItem
                          nodeId={eachChildAsset._id} key={childAssetInd} label={eachChildAsset.name} onClick={() => {
                            navigate(`/childAsset/${eachChildAsset._id}`);
                          }}>
                          {/* chipsets Name */}
                          {eachChildAsset.chipsets.map((eachChipset, chipsetInd) =>
                            <TreeItem
                              nodeId={eachChipset._id} key={chipsetInd} label={eachChipset.name} onClick={() => {
                                navigate(`/chip/${eachChipset._id}`);
                              }}>
                              {/* sensors Name */}
                              {eachChipset.sensors.map((eachSensor, eachSensorInd) =>
                                <TreeItem
                                  nodeId={eachSensor._id} key={eachSensorInd} label={eachSensor.name} onClick={() => {
                                    navigate(`/sensor/${eachSensor._id}`);
                                  }}>
                                </TreeItem>
                              )}
                            </TreeItem>
                          )}
                        </TreeItem>
                      )}
                    </TreeItem>
                  )}
                </TreeItem>
              )
              }
            </TreeItem>
          ) :
            <TreeItem nodeId="25" label="Loading..." >
            </TreeItem>
          }
        </TreeItem>

      </TreeView>
    </Grid>
  );
}