import React, { useContext } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import TextField from '@material-ui/core/TextField';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import { useState } from 'react';
import Accordion from '@material-ui/core/Accordion';
import AccordionSummary from '@material-ui/core/AccordionSummary';
import AccordionDetails from '@material-ui/core/AccordionDetails';
import AddCircleOutlineIcon from '@material-ui/icons/AddCircleOutline';
import { GlobalContext } from '../../context/Context';
import axios from "axios"

const useStyles = makeStyles((theme) => ({
    root: {
        flexGrow: 1,
        width: "100 %",
    },
    heading: {
        fontSize: theme.typography.pxToRem(15),
        fontWeight: theme.typography.fontWeightRegular,
    },
    paper: {
        padding: theme.spacing(2),
        textAlign: 'center',
        color: theme.palette.text.secondary,
    },
    textField: {
        marginLeft: theme.spacing(1),
        marginRight: theme.spacing(1),
    },
}));

export default function AddGroup({ companyId }) {

    const classes = useStyles();

    // Local States 
    let [name, setName] = useState();
    let [accountNumber, setAccountNumber] = useState();
    let { dispatch, state } = useContext(GlobalContext);

    // Sending data to server
    const handleSubmit = (e) => {
        e.preventDefault();
       
        axios.post(state.baseUrl + "/group", {
            name, companyId
        }).then((res) => {
            dispatch({
                type: "GROUP_ADD",
            })
            alert(res.request.response)
        }
        )
        setName("");
    }
    return (
        <>
            <Accordion style={{ border: "1px solid grey", width: "40rem", width: "42rem", marginBottom: '1rem', textAlign: "left", backgroundColor: "lavender", marginTop: "1rem", }}>
                <AccordionSummary
                    aria-controls="panel1a-content"
                    id="panel1a-header"
                    style={{ display: 'flex', justifyContent: "center", alignItems: "center" }}
                >
                    <Typography variant="h6" style={{ color: "Black" }}>
                        Add New Group
                    </Typography>
                    <AddCircleOutlineIcon style={{ color: "green", paddingTop: "0.3rem", paddingLeft: "0.5rem" }}></AddCircleOutlineIcon>
                </AccordionSummary>
                <AccordionDetails >
                    <div style={{ textAlign: "left" }}>
                        <Typography variant="h4" gutterBottom style={{ color: "black" }}>
                            Register Group
                            </Typography>
                        <form autoComplete="off" onSubmit={handleSubmit}>
                            <TextField className={classes.textField} value={name} onInput={(e) => setName(e.target.value)} required label="Name" />
                            <TextField className={classes.textField} value={accountNumber} onInput={(e) => setAccountNumber(e.target.value)} label="Account Number" />
                            <span style={{ width: "100%", display: "inline-block", marginTop: "1.5rem" }}>
                                <Button type="submit" variant="contained" color="primary"
                                    style={{ width: "50%" }}>
                                    Create Group
                                    </Button>
                            </span>
                        </form>
                    </div>
                </AccordionDetails>
            </Accordion>
        </>
    )
}
