import React, { useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import { useEffect } from 'react';
import { useContext } from 'react';
import { GlobalContext } from '../../context/Context';
import { useParams } from 'react-router';
import axios from "axios"
import ChildAssets from '../childAsset/ChildAssets';
import TextField from '@material-ui/core/TextField';

const useStyles = makeStyles((theme) => ({
    textField: {
        marginLeft: theme.spacing(1),
        marginRight: theme.spacing(1),
    },

}));

export default function Asset() {
    const classes = useStyles();
    let [data, setData] = useState();
    // Global State
    let { state } = useContext(GlobalContext);
    const { assetId } = useParams();

    // Hitting API code
    useEffect(() => {
        (async () => {
            const response = await axios.get(state.baseUrl + "/asset/" + assetId);
            setData(response.data)
        })()
    }, [state.refreshChildAssests, assetId]);
    // console.log("Server", data)
    return (
        <>
            {data ?
                <div style={{ width: "100%", marginBottom: '1rem', textAlign: "left" }}>
                    <Typography variant="h4" style={{ color: "black", marginTop: "1rem", marginBottom: "1rem" }}>
                        {data.name} :
                    </Typography>
                    <TextField className={classes.textField} InputProps={{ disableUnderline: true }} label="Plate Number" value={data.plateNumber} disabled />
                    <TextField className={classes.textField} InputProps={{ disableUnderline: true }} label="Serial Number" value={data.serialNumber} disabled />
                    <TextField className={classes.textField} InputProps={{ disableUnderline: true }} label="Make" value={data.make} disabled />
                    <TextField className={classes.textField} InputProps={{ disableUnderline: true }} label="Model" value={data.model} disabled />
                    <TextField className={classes.textField} InputProps={{ disableUnderline: true }} label="Year" value={data.year} disabled />
                    <TextField className={classes.textField} InputProps={{ disableUnderline: true }} label="Oil Type" value={data.oilType} disabled />
                    <TextField className={classes.textField} InputProps={{ disableUnderline: true }} label="Type" value={data.type} disabled />
                    <TextField className={classes.textField} InputProps={{ disableUnderline: true }} label="Horse Power" value={data.horsePower} disabled />
                </div>
                : <h2>Loading Asset...</h2>
            }
            <ChildAssets></ChildAssets>
        </>
    )
}