import "./App.css";
import LeftBar from "./components/LeftBar";
import RightBar from './components/RightBar';
import Grid from '@material-ui/core/Grid';
import ContextProvider from "./context/Context"
import { BrowserRouter as Router } from 'react-router-dom';
import Background from "./images/background.jpg"

function App() {
  return (
    <Router>
      <ContextProvider>
        <Grid container style={{backgroundImage: `url(${Background})`,minHeight:"100vh",backgroundRepeat:"no-repeat",backgroundSize:"cover",backgroundPosition:"center",backgroundAttachment:"fixed"}} >
          <>
            <LeftBar></LeftBar>
            <RightBar></RightBar>
          </>
        </Grid>
      </ContextProvider>
    </Router>
  );
}

export default App;
