import React, { useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';
import { useEffect } from 'react';
import { useContext } from 'react';
import { GlobalContext } from '../../context/Context';
import DeleteIcon from '@material-ui/icons/Delete';
import Button from '@material-ui/core/Button';
import { useNavigate, useParams } from 'react-router';
import axios from "axios"
import AddChip from './AddChip'
import TextField from '@material-ui/core/TextField';

const useStyles = makeStyles((theme) => ({
    root: {
        flexGrow: 1,
    },
    paper: {
        padding: theme.spacing(2),
        textAlign: 'center',
        color: theme.palette.text.secondary,
    },
    textField: {
        marginLeft: theme.spacing(1),
        marginRight: theme.spacing(1),
    },
    button: {
        margin: theme.spacing(1),
    },
}));
export default function Chips() {
    const classes = useStyles();
    const navigate = useNavigate()

    // States
    const { childAssetId } = useParams()
    let [data, setData] = useState([]);
    // Global State
    let { state, dispatch } = useContext(GlobalContext);

    // Deleting asset function
    function handleDelete(id) {
        axios.delete(state.baseUrl + `/chipset/${id}`)
            .then((res) => {
                alert(res.request.response)
                dispatch({
                    type: "CHIPS_ADD",
                })
            }
            ).catch((err) => alert(err.request.response))
    }

    // Hitting API code
    useEffect(() => {
        async function apiCall() {
            const response = await axios.get(state.baseUrl + "/chipsets?childAssetId=" + childAssetId);
            setData(response)
        }
        apiCall();
    }, [state.refreshChips, childAssetId]);

    return (
        <>
            {data.data ? data.data.map((eachChipset, ind) =>
                <Paper key={ind} className={classes.paper} style={{ border: "3px solid grey", width: "40rem", marginBottom: '1rem', textAlign: "left" }}>
                    <div key={ind + 1} style={{ display: "flex", justifyContent: "space-between" }}>
                        <Typography variant="h4" style={{ color: "black", cursor: "pointer" }}
                            onClick={() => {
                                navigate(`/chip/${eachChipset._id}`);
                            }}
                        >
                            {/* Group name here maping */}
                            {eachChipset.name}
                        </Typography>
                        <Button
                            variant="outlined"
                            color="secondary"
                            className={classes.button}
                            startIcon={<DeleteIcon />}
                            onClick={() => handleDelete(eachChipset._id)}
                        >
                            Delete
                            </Button>
                    </div>
                    <TextField className={classes.textField} InputProps={{ disableUnderline: true }} label="Make" value={data.make} disabled />
                    <TextField className={classes.textField} InputProps={{ disableUnderline: true }} label="Model" value={data.model} disabled />
                    <TextField className={classes.textField} InputProps={{ disableUnderline: true }} label="Year" value={data.year} disabled />
                    <TextField className={classes.textField} InputProps={{ disableUnderline: true }} label="Serial Number" value={data.serialNo} disabled />
                    <TextField className={classes.textField} InputProps={{ disableUnderline: true }} label="Additional Number" value={data.additionalNo} disabled />
                
                </Paper>)
                : <h2>Loading Chips.......</h2>}

            <AddChip childAssetId={childAssetId}></AddChip>
        </>
    )
}
