import React, { useContext } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import TextField from '@material-ui/core/TextField';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import { useState } from 'react';
import Accordion from '@material-ui/core/Accordion';
import AccordionSummary from '@material-ui/core/AccordionSummary';
import AccordionDetails from '@material-ui/core/AccordionDetails';
import AddCircleOutlineIcon from '@material-ui/icons/AddCircleOutline';
import { GlobalContext } from '../../context/Context';
import axios from "axios";

const useStyles = makeStyles((theme) => ({
    root: {
        flexGrow: 1,
        width: "100 %",
    },
    heading: {
        fontSize: theme.typography.pxToRem(15),
        fontWeight: theme.typography.fontWeightRegular,
    },
    paper: {
        padding: theme.spacing(2),
        textAlign: 'center',
        color: theme.palette.text.secondary,
    },
    textField: {
        marginLeft: theme.spacing(1),
        marginRight: theme.spacing(1),
    },
}));

export default function AddCompany() {
    const classes = useStyles();

    let [name, setName] = useState();
    let [address, setAddress] = useState("");
    let [email, setEmail] = useState("");
    let [website, setWebsite] = useState("");
    let [phone, setPhone] = useState("");
    let { dispatch, state } = useContext(GlobalContext);

    // Sending data to server
    const handleSubmit = (e) => {
        e.preventDefault();
        const obj = {
            name,
            address,
            email,
            website,
            phone
        }
        axios.post(state.baseUrl + "/company", obj)
            .catch((err) => {
                alert(err.request.response);
            })
            .then((axResponse) => {
                alert(axResponse.data);
                dispatch({
                    type: "COMPANY_ADD",
                })

            })
        setName("");
        setAddress("");
        setEmail("");
        setWebsite("");
        setPhone("");
    }
    return (
        <>
            <Accordion onChange={(obj, isExpanded) => isExpanded ? window.setTimeout(() => { window.scrollTo(0, document.body.scrollHeight) }, 200) : null}
                style={{ border: "1px solid grey", maxWidth: "42rem", marginBottom: '1rem', textAlign: "left" }}>
                <AccordionSummary
                    aria-controls="panel1a-content"
                    id="panel1a-header"
                    style={{ display: 'flex', justifyContent: "center", alignItems: "center" }}
                >
                    <Typography variant="h6" style={{ color: "Black" }}>
                        Add New Company
                    </Typography>
                    <AddCircleOutlineIcon style={{ color: "green", paddingTop: "0.3rem", paddingLeft: "0.5rem" }}></AddCircleOutlineIcon>
                </AccordionSummary>
                <AccordionDetails>

                    <div style={{ textAlign: "left" }}>
                        <Typography variant="h4" gutterBottom style={{ color: "black" }}>
                            Register Company
                            </Typography>
                        <form autoComplete="off" onSubmit={handleSubmit}>
                            <TextField className={classes.textField} required value={name} onInput={(e) => setName(e.target.value)} id="name" label="Name" />
                            <TextField value={address} onInput={(e) => setAddress(e.target.value)} className={classes.textField} id="address" label="Address" />
                            <TextField value={email} onInput={(e) => setEmail(e.target.value)} className={classes.textField} id="email" label="email" />
                            <TextField value={website} onInput={(e) => setWebsite(e.target.value)} className={classes.textField} id="website" label="Website" />
                            <TextField value={phone} onInput={(e) => setPhone(e.target.value)} className={classes.textField} id="phone" label="Phone" />
                            {/* Button Span */}
                            <span style={{ width: "100%", display: "inline-block", marginTop: "1.5rem" }}>
                                <Button type="submit" variant="contained" color="primary"
                                    style={{ width: "50%" }}>
                                    Submit
                                </Button>
                            </span>
                        </form>
                    </div>
                </AccordionDetails>
            </Accordion>
        </>
    )
}
